import { useEffect, useState } from 'react'
import {useDispatch, useSelector} from "react-redux"
import Select from 'react-select'
import { selectThemeColors } from '@utils'
import { DragDrop } from '@uppy/react'
import Uppy from '@uppy/core'
import '@uppy/core/dist/style.css'
import classnames from 'classnames'
import { useForm, Controller } from 'react-hook-form'
import thumbnailGenerator from '@uppy/thumbnail-generator'
// import "./styles.scss"
import './styles copy.scss'
import '@styles/react/libs/file-uploader/file-uploader.scss'
import { Card, CardHeader, CardTitle, CardBody, Row, Col, Label, Button} from 'reactstrap'
import {addfile, getAllFootballTeams} from "../../store/actions"
// import Table from  "./list/Table"
const SelectReact = () => {
  const defaultValues = {
    reactSelect: null
  }
  const { handleSubmit, control } = useForm({ defaultValues })
  
    const store = useSelector((state) => state.corporatefixture)
    const [val, setVal] = useState(false)
    const [img, setImg] = useState(null)
    const [imageError] = useState(false)
    const [teamName, setTeamName] = useState("")
    const [teamsList] = useState([])
    const dispatch = useDispatch()

    useEffect(() => {
      dispatch(getAllFootballTeams())
    }, [teamsList])


    const uppy = new Uppy({
      meta: { type: 'avatar' },
      restrictions: { maxNumberOfFiles: 1 },
      autoProceed: true
    })
  
    uppy.use(thumbnailGenerator)
  
    uppy.on('thumbnail:generated', (file, preview) => {
      setImg(preview)
    })

    const teams = store.allTeams.map((ser) => {
      return { value: ser.id, label: ser.team_sn }
    })   

    const submit = (e) => {
      setVal(true)
      e.preventDefault()
        const param = new FormData()
        param.append('series_id', teamName.value)
        param.append('file', img)
        dispatch(addfile(param)) 
    }
    

  return (
    <Card>
      <CardHeader>
        <CardTitle tag='h4'>{store.isloading }</CardTitle> 
      </CardHeader>
      <CardBody>
        <Row>
          <Col className='mb-1' md='6' sm='12'>
            <Label>series</Label>
            <Controller
              control={control}
              render={({ field: { ...field } }) => (
                <Select
                  {...field}
                  onChange={(e) => setTeamName(e)}
                  options={teams}
                  placeholder='Select football series'
                  theme={selectThemeColors}
                  className={classnames('react-select', { 'is-invalid':  teamName === "" && val })}
                  classNamePrefix='select'
                  isClearable={true}
                  />
                  )}
                  name='reactSelect'
            />
             {teamName === "" && val ?  <h5 className='text-danger'>Please enter a value</h5> : ""}
          </Col>
        </Row>
        <Row className="wrapper-add" >
        <Col className="add-post-edit">
         <div className={`mt-4 ${imageError ? 'img-error-post' : ''}`}>
         <DragDrop uppy={uppy}
           locale={{
            strings: {
              dropHereOr: 'Drop file or %{browse}',
              browse: 'browse'
            }
          }}
         />
        {img !== null ? <img className='rounded mt-2' src={img} alt='avatar' /> : null}
              </div>
              </Col>
             </Row>
             <div className="mt-3">
        <Button.Ripple 
        color='primary'
        onClick={(e) => handleSubmit(submit(e))}
        >submit</Button.Ripple>
        </div>                  
      </CardBody>
    </Card>
  )
}
export default SelectReact